import Vue from 'vue'
import VueRouter from 'vue-router'
import HOME from '../views/HOME.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'HOME',
        component: HOME,
        meta: {
            title: 'Xeni Siotou - Home'
        }
    },
    {
        path: '/ARCHITECTURE',
        name: 'ARCHITECTURE',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/ARCHITECTURE.vue'),
        meta: {
            title: 'Xeni Siotou - Architecture'
        }
    },
    {
        path: '/DRESSMAKING',
        name: 'DRESSMAKING',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/DRESSMAKING.vue'),
        meta: {
            title: 'Xeni Siotou - Dressmaking'
        }
    },
    {
        path: '/HANDICRAFT',
        name: 'HANDICRAFT',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/HANDICRAFT.vue'),
        meta: {
            title: 'Xeni Siotou - Handicraft'
        }
    },
    {
        path: '/ABOUT',
        name: 'ABOUT',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/ABOUT.vue'),
        meta: {
            title: 'Xeni Siotou - About'
        }
    },
    {
        path: '/project1',
        name: 'Project1',

        component: () => import('../views/Projects/Project1.vue')
    },
    {
        path: '/project2',
        name: 'Project2',

        component: () => import('../views/Projects/Project2.vue')
    },
    {
        path: '/project3',
        name: 'Project3',

        component: () => import('../views/Projects/Project3.vue')
    },
    {
        path: '/project4',
        name: 'Project4',

        component: () => import('../views/Projects/Project4.vue')
    },
    {
        path: '/lecture1',
        name: 'Lecture1',

        component: () => import('../views/Lectures/Lecture1.vue')
    },
    {
        path: '/lecture2',
        name: 'Lecture2',

        component: () => import('../views/Lectures/Lecture2.vue')
    },
    {
        path: '/lecture3',
        name: 'Lecture3',

        component: () => import('../views/Lectures/Lecture3.vue')
    }
]

const router = new VueRouter({
    routes
})

/**
 * Updates the title of the page according to the meta field on each router view as declared above.
 */
router.beforeEach((to, from, next) => {
    // This goes through the matched routes from last to first, finding the closest route with a title.
    // eg. if we have /some/deep/nested/route and /some, /deep, and /nested have titles, nested's will be chosen.
    const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);

    // Find the nearest route element with meta tags.
    const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);
    // const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

    // If a route with a title was found, set the document (page) title to that value.
    if (nearestWithTitle) document.title = nearestWithTitle.meta.title;

    // Remove any stale meta tags from the document using the key attribute we set below.
    Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el));

    // Skip rendering meta tags if there are none.
    if (!nearestWithMeta) return next();

    // Turn the meta tag definitions into actual elements in the head.
    nearestWithMeta.meta.metaTags.map(tagDef => {
        const tag = document.createElement('meta');

        Object.keys(tagDef).forEach(key => {
            tag.setAttribute(key, tagDef[key]);
        });

        // We use this to track which meta tags we create, so we don't interfere with other ones.
        tag.setAttribute('data-vue-router-controlled', '');

        return tag;
    })
        // Add the meta tags to the document head.
        .forEach(tag => document.head.appendChild(tag));

    next();
});

export default router
